<template>
  <el-row :gutter="40">
      <el-col :span="12" class="info">
            <Title title="" englishTitle="News" iconClass="icon-book"/>
             <a href="https://wiki.robo2025.com/" class="viewMoreItem" target="_blank"> >></a>
